import React from "react";
import Context from "../../Context";
import { injectIntl } from "react-intl";
import key from "../../util/key";
import { SearchOutlined } from "@ant-design/icons";
import DataTable from "../../components/Datatable/DataTable";
import DataTablePeriod from "../../components/Datatable/DataTablePeriod";
import DataTableSearch from "../../components/Datatable/DataTableSearch";
import { Helmet } from "react-helmet";
import { Checkbox } from "antd";

class DataTransfer extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      tableUuid: key(),
      columns: [],
      heartbeat: false,
    };
  }
  m = (key, prefix = "datatransfer.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };
  componentDidMount() {
    this.setColumns();
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.setColumns();
    }
  }

  heartbeatToggle = () => {
    this.setState((state) => {
      return { heartbeat: !state.heartbeat };
    });
  };

  setColumns = () => {
    this.setState({
      columns: [
        {
          width: "20%",
          title: this.m("datetime"),
          dataIndex: "datetime",
          sorter: true,
          render: (value) => {
            if (value) {
              const dt = new Date(value);
              return (
                <>
                  {dt.toLocaleDateString()}
                  <br />
                  {dt.toLocaleTimeString()}
                </>
              );
            }
          },

          date: true,
          filterDropdown: (props) => <DataTablePeriod {...props} />,
        },
        {
          text: true,
          title: this.m("action"),
          dataIndex: "action",
          render: (value) => <div className="gx-text-muted">{value}</div>,
          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{ color: filtered ? "#1890ff" : undefined }}
            />
          ),
        },

        {
          title: this.m("request"),
          dataIndex: "request",
          width: "30%",
          render: (value) => (
            <div
              className="gx-text-small gx-text-muted gx-font-weight-light"
              style={{ wordBreak: "break-all" }}
            >
              {JSON.stringify(value)}
            </div>
          ),
        },
        {
          title: this.m("response"),
          dataIndex: "response",
          width: "30%",
          render: (value) => (
            <div
              className="gx-text-small gx-text-muted gx-font-weight-light"
              style={{ wordBreak: "break-all" }}
            >
              {JSON.stringify(value)}
            </div>
          ),
        },
      ],
    });
  };

  render() {
    const forceFilter = this.props.identity
      ? { identity: this.props.identity }
      : null;

    const forceSearch = [];
    if (!this.state.heartbeat) {
      forceSearch.push({ field: "action", compare: "NEQ", value: "Heartbeat" });
    }

    const tableItself = (
      <>
        <Checkbox
          onChange={this.heartbeatToggle}
          checked={this.state.heartbeat}
          className="gx-mb-3"
        >
          {this.m("heartbeat")}
        </Checkbox>
        <DataTable
          forceSearch={forceSearch}
          forceFilter={forceFilter}
          uuid={this.state.tableUuid}
          columns={this.state.columns}
          path={"/ocpp/log/list"}
        />
      </>
    );
    if (this.props.tableItself) {
      return tableItself;
    }
    return (
      <div>
        <Helmet>
          <title>{this.m("datatransfer")}</title>
        </Helmet>
        <h2 className="title gx-mb-4">{this.m("datatransfer")}</h2>
        {tableItself}
      </div>
    );
  }
}

export default injectIntl(DataTransfer);
