import React from "react";
import Context from "../../Context";
import { injectIntl } from "react-intl";
import key from "../../util/key";
import DataTable from "../../components/Datatable/DataTable";
import DataTablePeriod from "../../components/Datatable/DataTablePeriod";
import { Helmet } from "react-helmet";

class Reservations extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      tableUuid: key(),
      columns: [],
    };
  }
  m = (key, prefix = "reservations.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };
  componentDidMount() {
    this.setColumns();
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.setColumns();
    }
  }

  setColumns = () => {
    this.setState({
      columns: [
        {
          title: this.m("id"),
          dataIndex: "id",
          sorter: true,
          defaultSortOrder: true,
          sortDirections: "descend",
        },
        {
          title: this.m("code"),
          dataIndex: "code",
          sorter: true,
        },
        {
          title: this.m("connectorid"),
          dataIndex: "connectorid",
          sorter: true,
        },
        {
          title: this.m("expirydate"),
          dataIndex: "expirydate",
          sorter: true,
          render: (value) => {
            if (value) {
              const dt = new Date(value);
              return (
                <>
                  {dt.toLocaleDateString()}
                  <br />
                  {dt.toLocaleTimeString()}
                </>
              );
            }
          },

          date: true,
          filterDropdown: (props) => <DataTablePeriod {...props} />,
        },
        {
          title: this.m("datestart"),
          dataIndex: "datestart",
          sorter: true,
          render: (value) => {
            if (value) {
              const dt = new Date(value);
              return (
                <>
                  {dt.toLocaleDateString()}
                  <br />
                  {dt.toLocaleTimeString()}
                </>
              );
            }
          },

          date: true,
          filterDropdown: (props) => <DataTablePeriod {...props} />,
        },
        {
          title: this.m("datestop"),
          dataIndex: "datestop",
          sorter: true,
          render: (value) => {
            if (value) {
              const dt = new Date(value);
              return (
                <>
                  {dt.toLocaleDateString()}
                  <br />
                  {dt.toLocaleTimeString()}
                </>
              );
            }
          },

          date: true,
          filterDropdown: (props) => <DataTablePeriod {...props} />,
        },
      ],
    });
  };

  render() {
    const forceFilter = this.props.chargepointId
      ? { chargepoint: this.props.chargepointId }
      : null;
    const tableItself = (
      <DataTable
        forceFilter={forceFilter}
        uuid={this.state.tableUuid}
        columns={this.state.columns}
        path={"/charge_point/reservation/list"}
      />
    );
    if (this.props.tableItself) {
      return tableItself;
    }
    return (
      <div>
        <Helmet>
          <title>{this.m("reservations")}</title>
        </Helmet>
        <h2 className="title gx-mb-4">{this.m("reservations")}</h2>
        {tableItself}
      </div>
    );
  }
}

export default injectIntl(Reservations);
