import React from "react";
import Context from "../../Context";
import { injectIntl } from "react-intl";
import { Button, message, Modal, Spin, Upload } from "antd";
import { PlusOutlined } from "@ant-design/icons";
import toBase64 from "../../util/toBase64";

const { maxFileSize } = window.appConfig;

class StationPhotosModal extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      pending: false,
      fileList: [],
      preview: false,
    };
  }

  componentDidMount() {
    this.load();
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevProps.chargepointId !== this.props.chargepointId) {
      this.load();
    }
  }

  m = (key, prefix = "stations.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };

  pendingStart = () => {
    if (this.props.onPendingStart) {
      this.props.onPendingStart();
      return;
    }
    this.setState({ pending: true });
  };

  pendingEnd = () => {
    if (this.props.onPendingEnd) {
      this.props.onPendingEnd();
      return;
    }
    this.setState({ pending: false });
  };

  onClose = () => {
    if (this.props.onClose) this.props.onClose();
  };

  load = async () => {
    this.pendingStart();
    let fileList = [];
    if (this.props.chargepointId) {
      let files = await this.context.api.q(
        "/object/file",
        {
          id: this.props.chargepointId,
        },
        { toArray: true }
      );
      fileList = (files || [])
        .map((f) => {
          return {
            name: f.name,
            url: window.appConfig.apiDomain + f.link,
            stored: true,
          };
        })
        .filter((f) => f.url);
    }

    this.setState({ fileList, preview: false });
    this.pendingEnd();
  };

  save = async () => {
    this.pendingStart();

    let files = [];
    let uploadFiles = (this.state.fileList || []).reduce((uploadFiles, f) => {
      if (!f.stored && f.originFileObj) {
        uploadFiles.push(f.originFileObj);
      }
      return uploadFiles;
    }, []);

    for (let fileIndex in uploadFiles) {
      const file = uploadFiles[fileIndex];
      if (!file) continue;

      if (maxFileSize) {
        if (file.size > maxFileSize) {
          message.error(
            `${this.m("file.size.exceed", "")}: ${Math.round(
              maxFileSize / 1024
            )} ${this.m("kb", "")}`
          );
          this.pendingEnd();
          return;
        }
      }

      let { encoded, type } = await toBase64(file);

      files.push({
        name: file.name,
        path: "/public",
        size: file.size,
        date: file.lastModifiedDate,
        text: "",
        type: type,
        data: encoded,
      });
    }

    let result = await this.context.api.q("/object/file/set", {
      id: this.props.chargepointId,
      files,
    });

    if (!result) {
      message.error(this.m("save.fail", ""));
      this.pendingEnd();
    } else {
      message.success(this.m("save.success", ""));
      this.onClose();
      this.load();
    }
  };

  remove = async (file) => {
    if (!file.stored) return true;
    this.pendingStart();
    let result = await this.context.api.q("/object/file/set", {
      id: this.props.chargepointId,
      files: [{ name: file.name, size: -1 }],
    });
    if (!result) {
      message.error(this.m("save.fail", ""));
      this.pendingEnd();
    } else {
      message.success(this.m("save.success", ""));
      this.load();
    }
    return false;
  };

  handleChange = ({ fileList }) => {
    this.setState({ fileList });
  };

  handlePreview = async (file) => {
    if (!file.url && !file.preview) {
      this.pendingStart();
      file.preview = (await toBase64(file.originFileObj)).readerResult;
      this.pendingEnd();
    }

    this.setState({ preview: file.url || file.preview });
  };

  render() {
    const { visible } = this.props;
    const { pending, fileList, preview } = this.state;

    return (
      <Modal
        open={visible}
        footer={
          <>
            <Button
              loading={pending}
              type="primary"
              htmlType="submit"
              className="gx-mb-0"
              onClick={this.save}
            >
              {this.m("save", "")}
            </Button>
          </>
        }
        onCancel={this.onClose}
      >
        <Spin spinning={pending}>
          <Upload
            beforeUpload={() => false}
            listType="picture-card"
            fileList={fileList}
            onChange={this.handleChange}
            onPreview={this.handlePreview}
            onRemove={this.remove}
          >
            {fileList.length >= 8 ? null : (
              <div>
                <PlusOutlined />
                <div
                  style={{
                    marginTop: 8,
                  }}
                >
                  {this.m("upload", "")}
                </div>
              </div>
            )}
          </Upload>

          <Modal
            multiple
            open={!!preview}
            title={null}
            footer={null}
            onCancel={() => this.setState({ preview: false })}
            closeIcon={<span></span>}
          >
            <img
              alt=""
              style={{
                width: "100%",
              }}
              src={preview}
            />
          </Modal>
        </Spin>
      </Modal>
    );
  }
}
export default injectIntl(StationPhotosModal);
