import React from "react";
import Context from "../../Context";
import {withRouter} from "react-router-dom";
import {injectIntl} from "react-intl";
import {Helmet} from "react-helmet";
import {Button, Card, Col, Empty, Row, Spin, Tabs} from "antd";
import {
  ArrowRightOutlined,
  EnvironmentOutlined,
  FileImageOutlined, RetweetOutlined,
} from "@ant-design/icons";
import paramsHasChanged from "../../util/paramsHasChanged";
import {Link} from "react-router-dom";
import Flex from "../../util/Flex";
import Statelabel from "../../util/Statelabel";
import EditModal from "../../util/EditModal";
import stationEditForm from "./stationEditForm";
import Connectors from "../Connectors/Connectors";
import Reservations from "../Reservations/Reservations";
import StationTransactions from "../Transactions/StationTransactions";
import DataTransfer from "../DataTransfer/DataTransfer";
import StationGeoModal from "./StationGeoModal";
import StationPhotosModal from "./StationPhotosModal";
import {online} from "../../api/online";
import StationsMapProvider from "./StationsMapProvider";

class StationDetail extends React.Component {
  static contextType = Context;

  constructor(props) {
    super(props);

    const appConfig = window.appConfig || {};

    this.config = {
      map: appConfig.map,
    };

    this.state = {
      pending: false,
      stationId: false,
      item: {},
      access: {},

      editModalVisible: false,
      geoModalVisible: false,
      photoModalVisible: false,
    };
  }

  m = (key, prefix = "stations.") => {
    const {intl} = this.props;
    return intl.formatMessage({id: prefix + key});
  };

  pendingStart = () => {
    if (this.props.onPendingStart) {
      this.props.onPendingStart();
      return;
    }
    this.setState({pending: true});
  };

  pendingEnd = () => {
    if (this.props.onPendingEnd) {
      this.props.onPendingEnd();
      return;
    }
    this.setState({pending: false});
  };

  componentDidMount() {
    this.setParams();
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (paramsHasChanged(prevProps, this.props)) {
      this.setParams();
    }

    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.load();
    }
  }

  setParams = () => {
    const {param1: stationId} = this.props.match.params;
    this.setState(
      {
        stationId,
      },
      () => this.load()
    );
  };

  load = async () => {
    if (this.state.stationId) {
      this.pendingStart();
      let item = await this.context.api.q("/charge_point/get", {
        id: this.state.stationId,
      });
      let access = await this.context.api.q("/object/access/decode", {
        id: this.state.stationId,
      });
      this.setState({item, access}, () => this.pendingEnd());
    } else {
      this.setState({item: {}, access: {}});
    }
  };

  editModalToggle = () => {
    this.setState((state) => {
      return {editModalVisible: !state.editModalVisible};
    });
  };

  geoModalToggle = () => {
    this.setState((state) => {
      return {geoModalVisible: !state.geoModalVisible};
    });
  };

  photoModalToggle = () => {
    this.setState((state) => {
      return {photoModalVisible: !state.photoModalVisible};
    });
  };

  editModalFinish = () => {
    this.editModalToggle();
    this.load();
  };

  geoModalFinish = () => {
    this.geoModalToggle();
    this.load();
  };

  render() {
    const {pending, item, access} = this.state;

    const displayDetailProps = [
      "label",
      "identity",
      "description",
      "typename",
      "vendorname",
      "modelname",
      "version",
      "boxserialnumber",
      "serialnumber",
      "iccid",
      "imsi",
      "networkname",
      "connected",
      "connectionupdate",
      "lastupdate",
      "created",
    ];

    const DetailRow = ({item, property, connected}) => {
      let value = item?.[property];
      if (typeof value === "string") {
        if (property === 'lastupdate' || property === 'created' || property === 'connectionupdate') {
          value = new Date(value).toLocaleString();
        }
      } else if (typeof value === "boolean") {
        value = value ? this.m("true") : this.m("false")
      }
      if (!value) return null;
      return (
        <Row gutter={[16, 16]} className="gx-mb-2">
          <Col className="gx-text-muted gx-font-weight-light gx-pr-2" xs={10}>
            {property === 'connectionupdate' ? connected ? this.m(property) : this.m("disconnected") : this.m(property)}
          </Col>
          <Col xs={14}>{value}</Col>
        </Row>
      );
    };
    return (
      <div>
        <Helmet>
          <title>{item.label || this.m("station")}</title>
        </Helmet>
        <h2 className="title gx-mb-4">
          <Link className="gx-link" to="/crm/stations">
            {this.m("stations")}
          </Link>
          {item?.label && (
            <span>
              <span className="gx-mx-2 gx-text-small">
                <ArrowRightOutlined/>
              </span>
              {item?.label}
            </span>
          )}
        </h2>
        <EditModal
          essence="charge_point"
          id={item.id}
          onClose={this.editModalToggle}
          form={stationEditForm.call(this)}
          onFinish={this.editModalFinish}
          visible={this.state.editModalVisible}
        />
        <StationGeoModal
          key={item.id}
          chargepoint={item}
          visible={this.state.geoModalVisible}
          onClose={this.geoModalToggle}
          onFinish={this.geoModalFinish}
        />
        <StationPhotosModal
          chargepointId={item.id}
          chargepoint={item}
          visible={this.state.photoModalVisible}
          onClose={this.photoModalToggle}
        />
        <Spin spinning={pending}>
          {!item?.id && <Empty/>}
          {item?.id && (
            <div className="gx-mb-3">
              <Row gutter={[16, 16]}>
                <Col xl={8} xs={24}>
                  <Card className="gx-h-card">
                    <Flex between className="gx-mb-3">
                      <h4>{this.m("details")}</h4>
                      <Statelabel {...item} onFinish={this.load}/>
                    </Flex>
                    {displayDetailProps.map((prop) => (
                      <DetailRow key={prop} item={item} property={prop} connected={item.connected}/>
                    ))}
                    {access?.u && (
                      <>
                        <Button
                          onClick={this.editModalToggle}
                          className="gx-mb-0 gx-mt-4"
                        >
                          {this.m("edit", "")}
                        </Button>
                        <Button
                          title={this.m("geo")}
                          icon={<EnvironmentOutlined/>}
                          onClick={this.geoModalToggle}
                          className="gx-mb-0 gx-mt-4"
                        />
                        <Button
                          title={this.m("photos")}
                          icon={<FileImageOutlined/>}
                          onClick={this.photoModalToggle}
                          className="gx-mb-0 gx-mt-4"
                        />
                      </>
                    )}
                  </Card>
                </Col>
                <Col xl={16} xs={24}>
                  <Card className="gx-h-card">
                    <h4 className="gx-mb-3">{this.m("connectors")}</h4>
                    <Connectors
                      chargepointAccess={access}
                      chargepointId={item.id}
                    />
                    <Button
                      title={this.m("update")}
                      className="gx-mb-0 gx-mr-0"
                      onClick={(e) => {
                        e.stopPropagation();
                        online.tick();
                      }}
                      icon={<RetweetOutlined/>}
                    />
                  </Card>
                </Col>
              </Row>
              <Row gutter={[16, 16]} className="gx-mt-3">
                <Col xl={16} xs={24}>
                  <Card className="gx-h-card">
                    <Tabs>
                      <Tabs.TabPane
                        tab={this.m("transactions")}
                        key="transactions"
                      >
                        <h4 className="gx-mb-3">{this.m("transactions")}</h4>
                        <StationTransactions
                          chargepointId={item.id}
                          tableItself
                        />
                      </Tabs.TabPane>
                      <Tabs.TabPane
                        tab={this.m("reservations")}
                        key="reservations"
                      >
                        <h4 className="gx-mb-3">{this.m("reservations")}</h4>
                        <Reservations chargepointId={item.id} tableItself/>
                      </Tabs.TabPane>

                      <Tabs.TabPane
                        tab={this.m("datatransfer")}
                        key="datatransfer"
                      >
                        <h4 className="gx-mb-3">{this.m("datatransfer")}</h4>
                        <DataTransfer identity={item.identity} tableItself/>
                      </Tabs.TabPane>
                    </Tabs>
                  </Card>
                </Col>
                <Col xl={8} xs={24}>
                  <Card className="gx-h-card">
                    <h4 className="gx-mb-3">{this.m("location")}</h4>
                    <StationsMapProvider
                      stations={[item]}
                      showMoreButton={false}
                      ocpp={false}
                      className="gx-st-map small"
                    />
                  </Card>
                </Col>
              </Row>
            </div>
          )}
        </Spin>
      </div>
    );
  }
}

export default withRouter(injectIntl(StationDetail));
