import React from "react";
import Context from "../../Context";
import { injectIntl } from "react-intl";
import { Button, Form, Input, message, Modal, Spin } from "antd";
class StationGeoModal extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      pending: false,
    };
  }

  m = (key, prefix = "stations.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };

  pendingStart = () => {
    if (this.props.onPendingStart) {
      this.props.onPendingStart();
      return;
    }
    this.setState({ pending: true });
  };

  pendingEnd = () => {
    if (this.props.onPendingEnd) {
      this.props.onPendingEnd();
      return;
    }
    this.setState({ pending: false });
  };

  onClose = () => {
    if (this.props.onClose) this.props.onClose();
  };

  save = async (data) => {
    this.pendingStart();
    let result = await this.context.api.q("/object/geolocation/set", {
      id: this.props.chargepoint?.id,
      coordinates: [
        {
          latitude: data.lat || 0,
          longitude: data.lng || 0,
        },
      ],
    });
    if (!result) {
      message.error(this.m("save.fail", ""));
    } else {
      message.success(this.m("save.success", ""));
      if (this.props.onFinish) this.props.onFinish();
    }
    this.pendingEnd();
  };

  render() {
    const { visible, chargepoint } = this.props;
    const { pending } = this.state;

    let geo = chargepoint?.geo || [];

    return (
      <Modal open={visible} footer={null} onCancel={this.onClose}>
        <Spin spinning={pending}>
          <Form
            key={chargepoint?.id}
            layout="vertical"
            initialValues={{
              lat: geo[0]?.latitude,
              lng: geo[0]?.longitude,
            }}
            onFinish={this.save}
            scrollToFirstError
          >
            <Form.Item label={this.m("lat")} name="lat">
              <Input type="number" />
            </Form.Item>
            <Form.Item label={this.m("lng")} name="lng">
              <Input type="number" />
            </Form.Item>
            <Button
              loading={pending}
              type="primary"
              htmlType="submit"
              className="gx-mb-0"
            >
              {this.m("save", "")}
            </Button>
          </Form>
        </Spin>
      </Modal>
    );
  }
}
export default injectIntl(StationGeoModal);
