import React from "react";
import PlainSelect from "../../util/PlainSelect";
import { Form, Input } from "antd";
const { TextArea } = Input;

export default function connectorEditForm() {
  let formItems = {
    label: {
      rules: [
        {
          required: true,
        },
      ],
    },
    charger: {
      rules: [
        {
          required: true,
        },
      ],
      label: this.m("chargername"),
      component: <PlainSelect enabledOnly cache query="/charger/list" />,
    },
    type: {
      rules: [
        {
          required: true,
        },
      ],
      label: this.m("typename"),
      component: <PlainSelect labelKey="name" cache query="/connector/type" />,
    },
    mode: {
      rules: [
        {
          required: true,
        },
      ],
      label: this.m("mode"),
      component: <PlainSelect enabledOnly cache query="/mode/list" />,
    },
    connectorid: {
      rules: [
        {
          required: true,
        },
      ],
    },
    description: {
      component: <TextArea />,
    },
  };
  return (
    <>
      {Object.entries(formItems).map(([key, item]) => {
        let label = item.label || this.m(key);
        let component = item.component || <Input />;

        return (
          <Form.Item key={key} rules={item.rules} label={label} name={key}>
            {component}
          </Form.Item>
        );
      })}
    </>
  );
}
