import React from "react";
import Context from "../../Context";
import { injectIntl } from "react-intl";
import key from "../../util/key";
import {
  PlusOutlined,
  SearchOutlined,
} from "@ant-design/icons";
import DataTable from "../../components/Datatable/DataTable";
import DataTableSearch from "../../components/Datatable/DataTableSearch";
import Statelabel from "../../util/Statelabel";
import { Button, message } from "antd";
import EditModal from "../../util/EditModal";
import connectorEditForm from "./connectorEditForm";
import Flex from "../../util/Flex";
import { connect } from "react-redux";
import ConnectorButtons from "./ConnectorButtons";

class Connectors extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      tableUuid: key(),
      columns: [],

      pending: false,

      editModalVisible: false,
      editModalId: null,
    };
  }
  m = (key, prefix = "connectors.") => {
    const { intl } = this.props;
    return intl.formatMessage({ id: prefix + key });
  };
  componentDidMount() {
    this.setColumns();
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.setColumns();
    }
  }

  pendingStart = () => {
    if (this.props.onPendingStart) {
      this.props.onPendingStart();
      return;
    }
    this.setState({ pending: true });
  };
  pendingEnd = () => {
    if (this.props.onPendingEnd) {
      this.props.onPendingEnd();
      return;
    }
    this.setState({ pending: false });
  };

  onMethodFinish = () => {
    this.setState({
      tableUuid: key(),
    });
    if (this.props.onMethodFinish) {
      this.props.onMethodFinish();
    }
  };

  editModalToggle = (editModalId = null) => {
    this.setState((state) => {
      return { editModalVisible: !state.editModalVisible, editModalId };
    });
  };

  editModalFinish = () => {
    this.editModalToggle();
    this.onMethodFinish();
  };

  chargeToggle = async (item = {}, value) => {
    if (item.id) {
      this.pendingStart();
      let result = false;
      if (value) {
        result = await this.context.api.q("/connector/remote/stop", {
          id: item.id,
        });
      } else {
        result = await this.context.api.q("/connector/remote/start", {
          id: item.id,
        });
      }
      this.pendingEnd();
      if (result === false) {
        message.error(this.m("method.fail", ""));
      } else {
        message.success(this.m("method.success", ""));
      }
      this.onMethodFinish();
    }
  };

  reserveToggle = async (item = {}, value) => {
    if (item.id) {
      this.pendingStart();
      let result = false;
      if (value) {
        result = await this.context.api.q("/connector/reserve/cancel", {
          id: item.id,
        });
      } else {
        result = await this.context.api.q("/connector/reserve/now", {
          id: item.id,
        });
      }
      this.pendingEnd();
      if (result === false) {
        message.error(this.m("method.fail", ""));
      } else {
        message.success(this.m("method.success", ""));
      }
      this.onMethodFinish();
    }
  };

  setColumns = () => {
    let canAdd = this.props.user?.access?.connector?.c ? true : false;
    let canEdit = this.props.chargepointAccess?.u ? true : false;
    this.setState({
      columns: [
        {
          text: true,
          title: (
            <div style={{ whiteSpace: "nowrap" }}>
              {canAdd && (
                <Button
                  className="gx-mr-3"
                  onClick={(e) => {
                    e.stopPropagation();
                    this.editModalToggle();
                  }}
                  icon={<PlusOutlined />}
                />
              )}
              {this.m("identity")}
            </div>
          ),
          dataIndex: "identity",
          sorter: true,
          width: "45%",
          render: (value, item) => (
            <>
              <div>
                {canEdit ? (
                  <div
                    className="gx-pointer gx-text-primary"
                    onClick={() => this.editModalToggle(item.id)}
                  >
                    {item.label && item.label !== value ? (
                      <>{item.label}</>
                    ) : (
                      value
                    )}
                  </div>
                ) : (
                  <div>
                    {" "}
                    {item.label && item.label !== value ? (
                      <>{item.label}</>
                    ) : (
                      value
                    )}
                  </div>
                )}
              </div>

              {item.description && (
                <div className="gx-font-weight-light gx-text-muted">
                  {item.description}
                </div>
              )}
            </>
          ),

          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{ color: filtered ? "#1890ff" : undefined }}
            />
          ),
        },
        {
          title: this.m("connectorid"),
          dataIndex: "connectorid",
          defaultSortOrder: true,
          sortDirections: "ascend",
          sorter: true,
          width: "1%",
          render: (value, item) => (
            <span className="gx-text-muted  gx-font-weight-light gx-mr-1">
              {value}
            </span>
          ),
        },
        {
          title: this.m("chargername"),
          dataIndex: "chargername",
          sorter: true,
          width: "45%",
          render: (value, item) => (
            <>
              <div>
                {value}
                {item.modename && (
                  <span className="gx-ml-2 gx-text-small gx-font-weight-light gx-text-muted">
                    {item.modename}
                  </span>
                )}
              </div>

              {item.typename && (
                <div className="gx-font-weight-light gx-text-muted">
                  {item.typename}
                </div>
              )}
            </>
          ),
        },

        {
          title: this.m("statename"),
          dataIndex: "state",
          sorter: true,
          width: "10%",
          render: (value, item) => (
            <Flex between>
              <div style={{ whiteSpace: "nowrap" }}>
                <ConnectorButtons
                  m={this.m}
                  {...item}
                  chargeToggle={(value) => this.chargeToggle(item, value)}
                  reserveToggle={(value) => this.reserveToggle(item, value)}
                />
              </div>
              <div className="gx-text-right gx-ml-3">
                <Statelabel
                  onFinish={this.onMethodFinish}
                  {...item}
                  readOnly={!canEdit}
                />
              </div>
            </Flex>
          ),
        },
      ],
    });
  };

  render() {
    return (
      <>
        <EditModal
          essence="connector"
          onClose={this.editModalToggle}
          form={connectorEditForm.call(this)}
          onFinish={this.editModalFinish}
          visible={this.state.editModalVisible}
          id={this.state.editModalId}
          prepareData={({ data }) => {
            return {
              chargepoint: this.state.editModalId
                ? null
                : this.props.chargepointId,
              ...data,
            };
          }}
        />
        <DataTable
          forceFilter={{ chargepoint: this.props.chargepointId }}
          uuid={this.state.tableUuid}
          columns={this.state.columns}
          path={"/connector/list"}
          loading={this.state.pending}
        />{" "}
      </>
    );
  }
}

const mapStateToProps = (state) => ({
  user: state.user,
});
export default connect(mapStateToProps, null)(injectIntl(Connectors));
